* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-top: 100px;
  margin-left: 40px;
  /* margin-right: auto; */
  display: flex;
}

ul {
  list-style: none;
  padding: 0;
  margin-bottom: 0;
}
p {
  margin: 0;
  font-style: normal;
  font-weight: bold;
}
span {
  font-weight: normal;
}
.country-list li {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: 40px;
}

.country-info {
  margin-left: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
}

h3 {
  margin: 0;
}
